* {
  margin: 0;
}

.app {
  height: 97%;
  background-image: linear-gradient(to top right,#000000,#434343);
  margin-bottom:5px;
}

.app__posts{
  padding: 20px;
  display: flex;
  justify-content: center;
}

.app__postsRight{
  margin-left: 20px;
}

.floating{
  position: absolute;
	position: -webkit-sticky;
  position: sticky;
}

.app__header {
  background-color: white;
  padding: 20px;
  border-bottom: 1px solid lightgray;
  object-fit: contain;
  display: flex;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 1;
}

.app__headerImage{
  object-fit: contain;
  flex: 0;
  /* cursor: pointer; */
}

.app__loginContainer{
  display: flex;
  float: right;
}

.app__signup{
  display: flex;
  flex-direction: column;
}

footer{
  grid-template-columns: 0 1fr 0;
  position: sticky;
  bottom: 0;
  width: 100%;
  border-top: 1px solid lightgray;
  height: 60px;
  max-height: 60px;
  background-color: white;
  text-align: center;
  justify-content: center;
  justify-content: space-between;
  object-fit: contain;
}

footer h4{
  margin-top: 30px;
}

.footer__icons{
  display: flex;
}

.app__add-postImg, .app__home{
  
  cursor: pointer;
  background-color: transparent;
}

.footer__left{
  flex: 0;
}

  .app__home{
    height: 30px;
    padding-top: 10px;
    padding-left: 20px;
    float: left;
  }

.footer__middle{
  flex: 1;
}

.footer__middle Button{
  flex: 1;
  padding-top: 5px;
  margin-left: 5px;
}

  .app__add-postImg{
    height: 30px;
    padding-top: 10px;
  }

  .footer__right{
    flex: 0;
  }

.footer__avatar{
  color: black;
  margin-top: 10px;
  margin-right: 20px;
  max-height: 30px;
  max-width: 30px;
  cursor: pointer;
}


.post__thumbs{
  display: grid;
  grid-gap: 2px;
  grid-template-columns: auto auto auto;
}


@media (max-width: 768px) {
  .no-mobile {
    display: none;
  }

  
}